
import { defineComponent, onMounted, reactive } from 'vue'
import { useRouter } from "vue-router"
import { useStore } from "vuex"
import homeHeader from "@/components/header.vue"
import homeFooter from "@/components/footer.vue"
import { getBanner } from "@/api/index"
import { listData, bannerList } from "./list"

import { GlobalDataProps } from "../../store/index"
import { Toast } from 'vant'

export default defineComponent({
  name: 'my',
  components: {
    homeHeader,
    homeFooter
  },
  setup(props, context){

    const state = reactive({
      listData,
      bannerList,
      isLogin: 1, // 1 未登录 2 登录
    })

    const store = useStore<GlobalDataProps>()

    const router = useRouter()

    onMounted(() => {
      getBanner().then(res => {
        // console.log("res===", res)
      })
      state.listData = listData
      state.bannerList = bannerList
      // console.log("xxx---", state)
      if(localStorage.getItem("token")){
        state.isLogin = 2
      }else{
        state.isLogin = 1
      }
    })
    // 钱包-进入详情页 3
    const handleInfo = (item: any, index: any) => {
      // console.log("money-item", item.name)
    } 
    // 足迹-进入详情页面
    const handleEnter = (item: any, index: any) => {
      console.log("item", item)
      if(item.id == 7){
        router.push("address")
      }
    } 
    // 退出登录
    const handleExitLogin = () => {
      store.state.token = ""
      localStorage.removeItem("token")
      Toast("退出登录")
      state.isLogin = 1
    }

    // 进入登录
    const handleEnterLogin = () => {
      router.push("login")
    }

    return {
      state,
      handleInfo,
      handleEnter,
      handleEnterLogin,
      handleExitLogin
    }
  }
})
